<template lang="html">
  <div class="Header">
    <div class="banner">

    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App123'
    }
  }
}
</script>

<style lang="scss">
.Header{
  height: 300px;
  position: relative;
  .banner{
    background: url(../assets/banner1.jpg) center #000;
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-size: cover;
  }
}

</style>



