import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import App from './App'
import Top from './Top'
import sidebar from './sidebar'
Vue.use(VueResource)
Vue.use(VueRouter)
/* eslint-disable no-new */
// new Vue({
//   el: '#app',
//   template: '<App/>',
//   components: {App}
// })
const router = new VueRouter({
  routes: [
    { path: '',
      component: App,
      children: [
        { path: '/article', component: App }
      ]
    }
  ]
})
new Vue({
  el: '#header',
  template: '<top/>',
  components: {Top},
  router
})
new Vue({
  el: '#sidebar',
  template: '<sidebar>',
  components: {sidebar},
  router
})
new Vue({
  router
}).$mount('.outer')
Vue.http.get('http://localhost:81/static/test.jsonp').then(function (res) {
  let one = res.data
  console.log(one)
})



