<template>
  <section class="Content">
    <article class="article" v-for="article in articles">
      <div class="article-meta">
        <router-link to="/article" class="article-date">{{article.date}}</a>
      </div>
      <div class="article-inner">
        <header class="article-header">
          <h1>
            <a href="#" class="article-title">{{article.title}}</a>
          </h1>
        </header>
        <div class="article-entry">
          <p v-for="substance in article.substances">{{substance.row}}</p>
        </div>
      </div>
    </article>
  </section>
</template>

<script>
export default {
  name: 'Content',
  data () {
    return {
      articles: [
        {
          date: '2016-11-21',
          title: 'hello world',
          substances: [
            {row: '第一篇文章'},
            {row: '不知道写什么好呢'}
          ]
        },
        {
          date: '2016-11-24',
          title: '大家都是我的孙子',
          substances: [
            {row: '第二篇文章'},
            {row: '没错我叫冯爷爷'}
          ]
        }
      ]
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
.Content{
  display: block;
  .article{
    margin:50px 0;
    .article-date{
      display: inline-block;
      text-decoration: none;
      text-transform: uppercase;
      letter-spacing:2px;
      color:#999;
      margin-bottom:1em;
      margin-left: 5px;
      line-height: 1em;
      text-shadow: 0 1px #fff;
      font-weight: bold;
    }
    .article-inner{
      overflow: hidden;
      background-color: #fff;
      box-shadow:1px 2px 3px #ddd;
      .article-header{
        padding:20px 20px 0;
        .article-title{
          font-size: 2em;
          text-decoration: none;
          font-weight: bold;
          color:#555;
          line-height: 1.1em;
        }
      }
      .article-entry{
        color:#555;
        padding:0 20px;
        p,table{
          line-height: 1.6rem;
          margin:1.6em 0;
        }
      }
    }
  }
}
@media screen and (min-width: 768px){
  .Content{
    float: left;
    width: 73.3333333333%;
  }
}
.Content:before,.Content:after{
  content: "";
  display: table;
}
.Content:after{
  clear: both;
}

</style>



